import { WatchlistDataTableColoredCell } from "../../WatchlistDataTableCells/WatchlistDataTableColoredCell"
import { genericColumn } from "../../genericColumn"

function dailySentiment(tableName: string) {
  const title = "Current Sentiment"
  return genericColumn({
    endpoint: "companySentiment",
    columnKeys: ["dailySentiment"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip:
      "Captures the overall tone of news and events for a company on a given day, ranging from very negative to very positive. Range: [-1, 1].",
    cell: info => {
      const rawValue = info.row.original.financialData.companySentiment?.data?.dailySentiment ?? 0
      const sent = rawValue >= 0 ? "positive" : "negative"

      return (
        <WatchlistDataTableColoredCell
          queryResult={info.row.original.financialData.companySentiment}
          tooltip={"Explain the Daily Sentiment"}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What news is driving today's ${sent} sentiment for ${info.row.original.entityInfo.name}?`,
              value: info.row.original.financialData.companySentiment?.data?.dailySentiment,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={rawValue}
        >
          {rawValue === 0
            ? "Neutral"
            : `${rawValue > 0 ? "+" : "-"}${Number(Math.abs(rawValue)).toFixed(2)}`}
        </WatchlistDataTableColoredCell>
      )
    },
  })
}

export { dailySentiment }
